/*
  Hover: Text 
 
  hovers can easily be added to change the text color by adding `hover:` in front of the text color utility like `hover:primary`

  color_primary              - text color modified light to dark
  color_secondary              - text color modified light to dark
  color_info              - text color modified light to dark
  color_highlight              - text color success color
  color_success              - text color modified light to dark
  color_warning              - text color modified light to dark
  color_alert              - text color modified light to dark

